.dark-text {
  color: #454545; }

.white-text {
  color: white; }

.colorpicker .saturation {
  width: 100%;
  height: 300px;
  position: relative; }
  .colorpicker .saturation .pointer {
    --size: 15px;
    height: var(--size);
    width: var(--size);
    margin-top: calc(var(--size) * -0.5);
    margin-left: calc(var(--size) * -0.5);
    border-radius: 100%;
    border: 2px solid;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(#e2e2e2));
    background: -webkit-linear-gradient(top, white, #e2e2e2);
    background: -o-linear-gradient(top, white, #e2e2e2);
    background: linear-gradient(180deg, white, #e2e2e2); }

.colorpicker .hue {
  --height: 20px;
  width: 100%;
  height: var(--height);
  position: relative; }
  .colorpicker .hue .pointer {
    height: var(--height);
    width: 5px;
    background: white;
    border: 1px solid #9c9c9c; }



