.dark-text {
  color: #454545; }

.white-text {
  color: white; }

.color-box-container {
  text-align: center; }

.color-box {
  --size: 100px;
  width: var(--size);
  height: var(--size);
  margin: 10px auto;
  border-radius: 15px;
  outline: 0;
  border: 0;
  font-size: 24px; }
  .color-box * {
    opacity: 0;
    -webkit-transition: 200ms;
    -o-transition: 200ms;
    transition: 200ms; }
  .color-box:hover * {
    opacity: 1; }



